<template>
  <div class="courses">
    <!-- navbar -->
    <Navbar />
    <!-- courses -->
    <section class="section">
      <div class="container">
        <div class="columns is-multiline">
          <div class="column is-full mt-6" v-for="(cen, i) in centre" :key="i">
            <!-- filter if sub courses consists -->
            <div class="title is-2 has-text-centered" v-if="cen.course[0]">
              {{ cen.name }}
            </div>
            <div class="columns is-multiline mt-4">
              <div class="column is-4" v-for="(cou, i) in cen.course" :key="i">
                <article class="message">
                  <div class="message-header">
                    <p>
                      {{ cou.name }}
                    </p>
                  </div>
                  <div class="message-body">
                    {{ cou.about }}
                    <br />
                  </div>
                  <router-link
                    :to="{
                      name: 'subprogram',
                      params: {
                        id: cen.id,
                        subid: cou.id,
                        program: cou.program,
                      },
                    }"
                    class="button is-dark is-outlined m-3"
                  >
                    Know more
                  </router-link>
                </article>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <!-- footer -->
    <Footer />
  </div>
</template>
<script>
import Navbar from "@/components/Navbar.vue";
import Footer from "@/components/Footer.vue";
// import { mapActions } from "vuex";
export default {
  name: "courses",
  data() {
    return {
      id: null,
      subid: null,
      fetchCourses: false,
      centre: [],
      course: [],
    };
  },
  components: {
    Navbar,
    Footer,
  },
  async created() {
    // if (!this.$store.state.fetched) {
    //   await this.fetchData();
    // }
    let raw = await fetch(this.$store.state.api);
    let json = await raw.json();
    // console.log(json)
    json.forEach((cen) => {
      // centre
      this.centre.push({
        id: cen.id,
        name: cen.program,
      });
      // course:[]
      this.centre[cen.id - 1].course = [];
      // course.push()
      if (cen.subprogram != undefined) {
        cen.subprogram.forEach((cou) => {
          this.centre[cen.id - 1].course.push({
            id: cou.id,
            name: cou.program,
            about: cou.about,
          });
        });
      }
    });
    // json.forEach((e) => {
    //     // [],[{},{}]
    //     e.subprogram.forEach((prg) => {
    //         this.subprogram.push(prg);
    //     });
    // });
  },
  methods: {
    // ...mapActions(["fetchData"]),
  },
  computed: {
    // getPrograms() {
    //     return this.centre
    // },
  },
};
</script>
